.banner {
  background: #232222;

  &-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 25px 0px 45px;
  }

  &-avengers {
    display: flex;
    justify-content: center;
    align-items: center;

    &__text {
      margin-left: 83px;
      font-style: normal;
      font-weight: 700;
      font-size: 24px;
      line-height: 28px;

      color: #FFFFFF;
    }
  }
}